
.option-list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 20px;
}

.option {
    position: relative;
    cursor: pointer;
    padding: 20px;
    border: solid 1px rgb(201, 201, 201);
    margin-right: 5px;
    margin-bottom: 5px;
}

.flag {
    display: block;
    position: absolute;
    font-size: 10px;
    top: -6px;
    right: 6px;
    padding: 1px;
    background-color: white;
    font-weight: bold;
}

.correct {
    color: green;
    border: solid 1px green;
}

.incorrect {
    color: rgb(128, 0, 0);
    border: solid 1px rgb(128, 0, 0);
}

.selected {
    background-color: rgb(223, 223, 223);
}

.explanation {
    color: black;
}
